import React from 'react'
import { Link } from 'react-router-dom'
import FixtureCard from '../FixtureComponents/FixtureCard'

function Fextures({Features_list}) {
    
    return (
        <div className="container-fluid">
            
        {
            !Features_list ? '' :
            Features_list.map((fixture,i)=>
                <FixtureCard key={i} fixture={fixture} ></FixtureCard>
                )
        }
        
        
    </div>
    )
}

export default Fextures
